import React from 'react'
import styled from 'styled-components'

const CardHome: React.FC = ({ children }) => <StyledCardHome>{children}</StyledCardHome>

const StyledCardHome = styled.div`
  background-image: url(${process.env.PUBLIC_URL + '/images/13.png'});
  display: flex;
  flex: 1;
  flex-direction: column;
  background-size: cover;
  flex-direction: column;
  align-items: center;
`

export default CardHome
