import React from 'react'
import styled from 'styled-components'
import Button from '../../components/Button'
import Container from '../../components/Container'
import Page from '../../components/Page'
import PageHeader from '../../components/PageHeader'
import Spacer from '../../components/Spacer'
import Balances from './components/Balances'

const Home: React.FC = () => {
  return (
    <Page>
      <PageHeader
        icon={<img src={process.env.PUBLIC_URL + '/images/Lumos.png'} height={120} />}
        // title="MasterChef is Ready"
        // subtitle="Stake Uniswap LP tokens to claim your very own yummy SUSHI!"
      />
      <Spacer />

      <Container>
        <Balances />
      </Container>
      <Spacer size="lg" />
      {/* <StyledInfo>
        <b>Pro Tip</b>: SUSHI-ETH UNI-V2 LP token pool yields TWICE more token
        rewards per block.
      </StyledInfo> */}
      <Spacer size="lg" />
      <div
        style={{
          margin: '0 auto',
        }}
      >
        <Button text="See the Spell Book" to="/farms" variant="default" />
      </div>
    </Page>
  )
}

export default Home
