import React, { createContext, useEffect, useState } from 'react'

import { useWallet } from 'use-wallet'

import { Lumos } from '../../sushi'

export interface SushiContext {
  lumos?: typeof Lumos
}

export const Context = createContext<SushiContext>({
  lumos: undefined,
})

declare global {
  interface Window {
    lumoscharm: any
  }
}

const SushiProvider: React.FC = ({ children }) => {
  const { ethereum }: { ethereum: any } = useWallet()
  const [ lumos, setSushi] = useState<any>()

  // @ts-ignore
  window.lumos = lumos
  // @ts-ignore
  window.eth = ethereum

  useEffect(() => {
    if (ethereum) {
      const chainId = Number(ethereum.chainId)
      const sushiLib = new Lumos(ethereum, chainId, false, {
        defaultAccount: ethereum.selectedAddress,
        defaultConfirmations: 1,
        autoGasMultiplier: 1.5,
        testing: false,
        defaultGas: '6000000',
        defaultGasPrice: '1000000000000',
        accounts: [],
        ethereumNodeTimeout: 10000,
      })
      setSushi(sushiLib)
      window.lumoscharm = sushiLib
    }
  }, [ethereum])

  return <Context.Provider value={{ lumos }}>{children}</Context.Provider>
}

export default SushiProvider
