import BigNumber from 'bignumber.js'
import React, { useEffect, useState, useMemo, useCallback } from 'react'
import CountUp from 'react-countup'
import styled from 'styled-components'
import { useWallet } from 'use-wallet'
import CardHome from '../../../components/CardHome'
import CardContent from '../../../components/CardContent'
import Label from '../../../components/Label'
import Spacer from '../../../components/Spacer'
import Value from '../../../components/Value'
import Dial from '../../../components/Dial'
import Button from '../../../components/Button'
import CardIcon from '../../../components/CardIcon'
import Modal from '../../../components/Modal'
import ModalContent from '../../../components/ModalContent'
import ModalActions from '../../../components/ModalActions'
import Countdown, { CountdownRenderProps } from 'react-countdown'
import SushiIcon from '../../../components/SushiIcon'
import useAllEarnings from '../../../hooks/useAllEarnings'
import useAllStakedValue from '../../../hooks/useAllStakedValue'
import useFarms from '../../../hooks/useFarms'
import useTokenBalance from '../../../hooks/useTokenBalance'
import useSushi from '../../../hooks/useSushi'
import { getSushiAddress, getLumosPerBlock, getSushiSupply, getMasterChefContract, getLMSPriceMultiplier, getBlockTimestampLast, getLastHarvestBlock } from '../../../sushi/utils'
import { getBalanceNumber, getFullDisplayBalance } from '../../../utils/formatBalance'
import Web3 from 'web3'
import { isPropertySignature } from 'typescript'

const PendingRewards: React.FC = () => {
  const [start, setStart] = useState(0)
  const [end, setEnd] = useState(0)
  const [scale, setScale] = useState(1)

  const allEarnings = useAllEarnings()
  let sumEarning = 0
  for (let earning of allEarnings) {
    sumEarning += new BigNumber(earning)
      .div(new BigNumber(10).pow(18))
      .toNumber()
  }

  const [farms] = useFarms()
  const allStakedValue = useAllStakedValue()

  if (allStakedValue && allStakedValue.length) {
    const sumWeth = farms.reduce(
      (c, { id }, i) => c + (allStakedValue[i].totalWethValue.toNumber() || 0),
      0,
    )
  }

  useEffect(() => {
    setStart(end)
    setEnd(sumEarning)
  }, [sumEarning])

  return (
    <span
      style={{
        transform: `scale(${scale})`,
        transformOrigin: 'right bottom',
        transition: 'transform 0.5s',
        display: 'inline-block',
      }}
    >
      <CountUp
        start={start}
        end={end}
        decimals={end < 0 ? 4 : end > 1e5 ? 0 : 3}
        duration={1}
        onStart={() => {
          setScale(1.25)
          setTimeout(() => setScale(1), 600)
        }}
        separator=","
      />
    </span>
  )
}

const Balances: React.FC = () => {
  const [totalSupply, setTotalSupply] = useState<BigNumber>()
  const [lmsPerBlock, setlmsPerBlock] = useState<BigNumber>()
  const [lmsPriceMultiplier, setlmsPriceMultiplier] = useState<BigNumber>()
  const [lastHarvestBlock, setLastHarvestBlock] = useState(0)
  const [nextUpdate, setNextUpdate] = useState(0)

  const sushi = useSushi()
  const sushiBalance = useTokenBalance(getSushiAddress(sushi))
  const wallet = useWallet()
  var _block = wallet.getBlockNumber();

  const lastHarvest = _block > 0 && lastHarvestBlock > 0 ? (_block - lastHarvestBlock) : 0;

  const handleUpdateClick = useCallback(async () => {
    if (!sushi) return
    var _masterContract = getMasterChefContract(sushi);
    await _masterContract.methods.setLMSPriceMultiplier().send({ from: wallet.account, gas: 500000 })
  }, [wallet.account, sushi])

  const renderer = (countdownProps: CountdownRenderProps) => {
    const { hours, minutes, seconds } = countdownProps
    const paddedSeconds = seconds < 10 ? `0${seconds}` : seconds
    const paddedMinutes = minutes < 10 ? `0${minutes}` : minutes
    const paddedHours = hours < 10 ? `0${hours}` : hours
    return (
      <span>{paddedHours}:{paddedMinutes}:{paddedSeconds}</span>
    )
  }

  const dialValue = (nextUpdate - Date.now()) / (1000 * 60 * 60 * 24) * 100

  const DisplayUpdateProgress = useMemo(() => {
    return (
      <Dial size={90} value={dialValue} color="secondary">
        <StyledCountdown>
          <StyledCountdownText>
            {!nextUpdate ? "--" : <Countdown date={new Date(nextUpdate)} renderer={renderer} />}
          </StyledCountdownText>
          <Label text="Next update" />
        </StyledCountdown>
      </Dial>
    );
  }, [dialValue]);

  useEffect(() => {
    async function fetchTotalSupply() {
      const supply = await getSushiSupply(sushi)
      setTotalSupply(supply.minus(new BigNumber("295614000000000000000000")))
    }

    async function fetchPerBlock() {
      var _masterContract = getMasterChefContract(sushi);
      const _lmsPerBlock = await getLumosPerBlock(_masterContract)
      const _lmsPriceMultiplier = await getLMSPriceMultiplier(_masterContract)
      const _blockTimestampLast = await getBlockTimestampLast(_masterContract)
      var _lastHarvestBlock = await getLastHarvestBlock(_masterContract, wallet.account)
      setLastHarvestBlock(_lastHarvestBlock);
      setNextUpdate((parseInt(_blockTimestampLast) + (24 * 60 * 60)) * 1000);
      setlmsPriceMultiplier(new BigNumber(Web3.utils.fromWei(_lmsPriceMultiplier.toString())));
      setlmsPerBlock(new BigNumber(Web3.utils.fromWei(_lmsPerBlock.toString())).times(new BigNumber(Web3.utils.fromWei(_lmsPriceMultiplier.toString()))))
    }
    if (sushi) {
      fetchTotalSupply();
      fetchPerBlock();
    }
  }, [sushi, setTotalSupply])

  return (
    <StyledWrapper>
      <CardHome>
        <CardContent>
          <StyledBalances>
            <StyledBalance>
              <SushiIcon />
              <Spacer />
              <div style={{ flex: 1, marginTop: 53 }}>
                <Label text="Your Lumos Balance" />
                <Value
                  value={!!wallet && !!wallet.account ? getBalanceNumber(sushiBalance) : 'Locked'}
                />
              </div>
            </StyledBalance>
          </StyledBalances>
        </CardContent>
        <Footnote>
          Pending claim
          <FootnoteValue>
            <PendingRewards /> LMS
          </FootnoteValue>
        </Footnote>
        <Spacer size="lg" />
      </CardHome>
      <Spacer />

      <CardHome>
        <CardContent>
          <div style={{ flex: 1, marginTop: 53 }}></div>
          <Label text="Total Lumos Supply" />
          <Value
            value={totalSupply ? getBalanceNumber(totalSupply) : 'Locked'}
          />
        </CardContent>
        <Footnote>
          New rewards per block
          <FootnoteValue>{lmsPerBlock ? lmsPerBlock.toFixed(4, 1) : 'Locked'}</FootnoteValue>
        </Footnote>
        <Spacer size="lg" />
      </CardHome>

      <CardHome>
        <CardContent>
          <div style={{ flex: 1, marginTop: 10 }}></div>
          <Label text="Not claimed since :" />
          <Value
            value={!!wallet && !!wallet.account ? (lastHarvest == 0 ? "*********" : lastHarvest + ' blocks') : 'Locked'}
          />

          <Label text="Harvest Bonus multiplier" />
          <Value
            value={!!wallet && !!wallet.account ? (lastHarvest == 0 ? "*********" : Math.min((lastHarvest/67000),1).toPrecision(4) + 'X') : 'Locked'}
          />
        </CardContent>
        <Spacer size="lg" />
      </CardHome>
      <Spacer />
      <CardHome>
        <CardContent>
          <StyledUpdateContent>
            {DisplayUpdateProgress}
            <Spacer />
            <div style={{ flex: 1, margin: 10 }}>
              <Label text="Price Multiplier"> </Label>

              <Value value={lmsPriceMultiplier ? lmsPriceMultiplier.toNumber() + " x" : 'Locked'} />
              <Button size="sm"
                disabled={!wallet || !wallet.account}
                onClick={() => handleUpdateClick()}
                text="Update" variant="default"
              />
            </div>
          </StyledUpdateContent>
          <Spacer />

        </CardContent>

      </CardHome>
      <br />
           WARNING: Only 1 update transaction succeeds every 24 hours.
           Your transaction will likely fail.
    </StyledWrapper>

  )
}

const Footnote = styled.div`
    width: 300px;
    justify-content: space-between;
    display: flex;
`
const FootnoteValue = styled.div`
  font-family: 'bodoni';
  float: right;
`
const StyledWrapper = styled.div`
  max-width: 1050px;
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
`

const StyledBalances = styled.div`
  display: flex;
`

const StyledBalance = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
`
const StyledCountdown = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`

const StyledCountdownText = styled.span`
  color: black;
  font-size: 18px;
  font-weight: 700;
`
const StyledUpdateContent = styled.div`
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  flex-direction: row;  
  padding: ${(props) => props.theme.spacing[3]}px;
  margin-top:10px;
  margin-left:10px
`
export default Balances
