import React from 'react'
import styled from 'styled-components'

const Nav: React.FC = () => {
  return (
    <StyledNav>
      <StyledLink
        target="_blank"
        href="https://etherscan.io/address/0x154BC860a7A4668bca3B801DdB01D606baF7F8F5#code"
      >
        MasterWizard Contract
      </StyledLink>
      <StyledLink
        target="_blank"
        href="https://etherscan.io/address/0x034455C8a9882BF44C9704c780A55198E05BA559"
      >
        Lumos Contract
      </StyledLink>
      <StyledLink
        target="_blank"
        href="https://uniswap.info/pair/0x41B660B29C6014dC9E3165de4bE5E6F9DCD403E8"
      >
        Uniswap LMS-ETH
      </StyledLink>
      <StyledLink target="_blank" href="https://github.com/lumosfinance">
        Github
      </StyledLink>
      <StyledLink target="_blank" href="https://twitter.com/LumosFinance">
        Twitter
      </StyledLink>
      <StyledLink target="_blank" href="https://t.me/LumosFinance">
        Telegram
      </StyledLink>
    </StyledNav>
  )
}

const StyledNav = styled.nav`
  align-items: center;
  display: flex;
`

const StyledLink = styled.a`
  color: ${(props) => props.theme.color.white};
  padding-left: ${(props) => props.theme.spacing[3]}px;
  padding-right: ${(props) => props.theme.spacing[3]}px;
  text-decoration: none;
  &:hover {
    color: ${(props) => props.theme.color.grey[500]};
  }
`

export default Nav
